<template>
  <v-container>
    <v-row>
      <v-col cols="6">
        <v-textarea label="My done Quests" readonly v-model="myQuests" />
        <v-checkbox dense v-model="prettyExport" label="pretty"></v-checkbox>
      </v-col>
      <v-col cols="6">
        <v-textarea label="Your friends done Quests" v-model="input"></v-textarea>
        <v-checkbox dense v-model="prettyImport" label="pretty"></v-checkbox>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="6">
        <div
          v-for="q in myQ"
          :key="'me_'+q.id"
          :class="{common: friendQ.map(q=>q.id).indexOf(q.id)!==-1}"
        >{{q.name}} ({{q.from}})</div>
      </v-col>
      <v-col cols="6">
        <div
          v-for="q in friendQ"
          :key="'friend_'+q.id"
          :class="{common: myQ.map(q=>q.id).indexOf(q.id)!==-1}"
        >{{q.name}} ({{q.from}})</div>
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
export default {
  name: "Compare",
  data: () => ({
    prettyExport: false,
    prettyImport: false,
    input: ""
  }),
  computed: {
    myQ: function() {
      return this.staticData
        .getQuestsFiltered(this.$store.state.doneQuests, 0, 100, {})
        .sort((a, b) => (a.name == b.name ? 0 : a.name > b.name ? 1 : -1));
    },
    friendQ: function() {
      return this.staticData
        .getQuestsFiltered(this.friedQuests, 0, 100, {})
        .sort((a, b) => (a.name == b.name ? 0 : a.name > b.name ? 1 : -1));
    },
    myQuests: function() {
      if (this.prettyExport)
        return JSON.stringify(this.$store.state.doneQuests, null, 2);
      else return JSON.stringify(this.$store.state.doneQuests);
    },
    friedQuests: function() {
      try {
        return JSON.parse(this.input);
      } catch (e) {
        return [];
      }
    }
  },
  watch: {
    prettyImport: function(val) {
      try {
        if (val) this.input = JSON.stringify(JSON.parse(this.input), null, 2);
        else this.input = JSON.stringify(JSON.parse(this.input));
      } catch (e) {
        console.log(e);
      }
    }
  },
  methods: {}
};
</script>
<style scoped>
.common {
  color: green;
}
</style>